import React, { useEffect } from 'react';
import { CheckboxesInput, CheckboxesInputProps } from './checkboxesInput';
import { useConditionalField } from './conditionalForm';
import { useFieldError } from './useFieldError';
import { useMemoizedField } from './useMemoizedField';

export type CheckboxesFieldProps<T = any> = Omit<
  CheckboxesInputProps<T>,
  'name' | 'value' | 'onChange' | 'error'
> & {
  /**
   * Field name
   */
  readonly name: string;
};

/**
 * A list of Material-UI checkboxes which toggle the values in
 * Formik field.
 *
 * Component ensures Formik value is always an array.
 */
export function CheckboxesField<T>(props: CheckboxesFieldProps<T>) {
  const { name } = props;

  useConditionalField(name, 'CheckboxesField');

  const [field, , { setValue, setTouched }] = useMemoizedField<T[]>(name);
  const error = useFieldError(name);

  // Ensures Formik value is always an array.
  useEffect(() => {
    if (!Array.isArray(field.value)) {
      setValue([], false);
    }
  }, [field.value]);

  return (
    <CheckboxesInput
      {...props}
      value={field.value}
      onChange={value => {
        setTouched(true);
        setValue(value);
      }}
      error={error}
    />
  );
}
